.box > table > thead > tr > td {
    border: 1px solid rgb(0, 179, 255);
}

.box > table > thead > tr > td:nth-child(1) {
    width: 700px;
    text-align: center;
    padding: 10px;
}

.box > table > thead > tr > td:nth-child(2) {
    width: 100px;
    text-align: center;
    padding: 10px;
}

.box > table > thead > tr > td:nth-child(3) {
    width: 200px;
    text-align: center;
    padding: 10px;
}

.box > table > tbody > tr > td:nth-child(1) {
    width: 100px;
    height: 10px;
    text-align: center;
    padding: 10px;
}

.box > table > tbody > tr > td:nth-child(2) {
    width: 100px;
    height: 10px;
    text-align: center;
    padding: 10px;
}

.box > table > tbody > tr > td:nth-child(3) {
    width: 100px;
    height: 10px;
    text-align: center;
    padding: 10px;
}